html {
  box-sizing: border-box;
  width: 100vw;
  overflow-x: hidden;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  color: #212121;
  background-color: #fff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.startFilmsList {
  list-style-type: circle;
}
.trendingTitle {
  color: rgb(83, 80, 80);
  font-weight: 700;
  text-shadow: 1px 1px 2px darkslategray;
  padding: 20px;
}

.navLink {
  display: inline-block;
  padding: 40px 40px 0 40px;
}
.navLink a {
  font-size: 18px;
  font-weight: 900;
  text-decoration: none;
  margin-right: 30px;
}
.navLink a:hover {
  color: palevioletred;
}

/* =======================  INPIT*/
.searchFilminput {
  margin-left: 20px;
  width: 250px;
  height: 40px;
  border: solid 1px gray;
}
.searchFilminput::placeholder {
  font-size: 18px;
}
.form-group {
  position: relative;
  padding-top: 1.1rem;
}

label {
  position: absolute;
  top: 0;
  left: 20px;
  font-size: 14px;
  font-weight: 700;
  font-style: italic;
  opacity: 1;
  /* transform: translateY(0); */
  transition: all 0.3s ease-out;
}
.searchFilminput:placeholder-shown + label {
  opacity: 0;
  /* transform: translateY(1rem); */
}
.searchBtn {
  width: 100px;
  height: 40px;
}

.btnBack {
  width: 100px;
  height: 40px;
  margin-bottom: 20px;
}

.detailWrapper {
  padding: 5px 40px 5px 40px;
}
